// test/app/admin/layout.tsx
import Link from "next/link";
import { usePathname } from "next/navigation";
import { BarChart2, Users, Store, Settings } from "lucide-react";
import { Montserrat } from "next/font/google";

const montserrat = Montserrat({
  subsets: ["latin"],
  weight: ["800", "400"],
});

export default function AdminLayout({ children }) {
  const pathname = usePathname();

  return (
    <div className="flex min-h-screen">
      {/* Sidebar directement dans le layout */}
      <aside className="fixed top-0 left-0 h-full w-64 bg-[#1F1F1F] text-white shadow-2xl z-20">
        <div className="p-6">
          <h2 className={`text-3xl font-extrabold ${montserrat.className} text-[#FFD700]`}>
            Admin Panel
          </h2>
        </div>
        <nav className="mt-8">
          <ul className="space-y-2">
            <li>
              <Link
                href="/admin/dashboard"
                className={`flex items-center gap-3 px-6 py-4 transition-all duration-300 ${
                  pathname === "/admin/dashboard"
                    ? "bg-[#FFD700]/20 hover:bg-[#FFD700]/30"
                    : "hover:bg-[#FFD700]/10"
                }`}
              >
                <BarChart2 className="h-6 w-6 text-[#FFD700]" />
                <span className={`text-lg font-semibold ${montserrat.className}`}>
                  Dashboard
                </span>
              </Link>
            </li>
            <li>
              <Link
                href="/admin/gestion"
                className={`flex items-center gap-3 px-6 py-4 transition-all duration-300 ${
                  pathname === "/admin/gestion"
                    ? "bg-[#FFD700]/20 hover:bg-[#FFD700]/30"
                    : "hover:bg-[#FFD700]/10"
                }`}
              >
                <Users className="h-6 w-6 text-[#FFD700]" />
                <span className={`text-lg font-semibold ${montserrat.className}`}>
                  Gestion
                </span>
              </Link>
            </li>
            <li>
              <Link
                href="/admin/store"
                className={`flex items-center gap-3 px-6 py-4 transition-all duration-300 ${
                  pathname === "/admin/store"
                    ? "bg-[#FFD700]/20 hover:bg-[#FFD700]/30"
                    : "hover:bg-[#FFD700]/10"
                }`}
              >
                <Store className="h-6 w-6 text-[#FFD700]" />
                <span className={`text-lg font-semibold ${montserrat.className}`}>
                  Magasin
                </span>
              </Link>
            </li>
            <li>
              <Link
                href="/admin/settings"
                className={`flex items-center gap-3 px-6 py-4 transition-all duration-300 ${
                  pathname === "/admin/settings"
                    ? "bg-[#FFD700]/20 hover:bg-[#FFD700]/30"
                    : "hover:bg-[#FFD700]/10"
                }`}
              >
                <Settings className="h-6 w-6 text-[#FFD700]" />
                <span className={`text-lg font-semibold ${montserrat.className}`}>
                  Param√®tres
                </span>
              </Link>
            </li>
          </ul>
        </nav>
      </aside>

      {/* Contenu des pages */}
      <div className="flex-1 ml-64 bg-gradient-to-br from-[#F0F8FF] via-[#E6F0FA] to-[#B0E0E6]/30 py-12 px-4 sm:px-6 lg:px-8 relative overflow-hidden">
        {children}
      </div>
    </div>
  );
}